<template>
  <div>
    <div v-if="loading" class="d-flex justify-content-center my-5">
      <div class="spinner-border" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
    </div>
    <h3
      class="text-center m-2 p-3 rounded bg-dark text-light text-uppercase border"
    >
      Bill no : {{ $route.params.billNumber }}
    </h3>
    <div v-if="bill.billPhoto" class="bill-view">
      <img class="img-fluid" :src="bill.billPhoto" :alt="bill.billNumber" />
    </div>
    <div class="d-grid"></div>
    <div
      v-if="!bill.billPhoto"
      class="text-center alert alert-warning p-4 border"
    >
      <img src="@/assets/alert-triangle.svg" alt="" />
      <p>No Bill Attachment</p>
    </div>
    <div class="d-grid p-3">
      <a
        v-if="bill"
        class="btn btn-primary btn-lg mb-2"
        :download="'Bill-' + bill.billNumber"
        :href="bill.billPhoto"
        >Download Bill</a
      >
      <button
        type="button"
        @click.prevent="$router.go(-1)"
        class="btn btn-light"
      >
        Back
      </button>
    </div>
    <div v-if="!bill && !loading">
      <p class="alert alert-danger text-center">Bill Not Found</p>
    </div>
  </div>
</template>

<script>
import http from "../common/http-common";
export default {
  data() {
    return {
      bill: null,
      loading: false,
    };
  },
  async created() {
    this.loading = true;
    const result = await http.get("/bills/" + this.$route.params.billNumber);
    if (result.data.success) {
      this.bill = result.data.bill;
      this.loading = false;
    } else {
      this.loading = false;
    }
  },
};
</script>

<style lang="scss" scoped></style>
